.footer-tenth {
    display: block;
    background-color: #24aa98;
    margin-bottom: 0;
    h1 {
        margin: 0;
        padding: 62px;
        font-size: 50px;
        font-weight: bold;
        color: #fff;
        text-align: center;
    }
    span {
        display: block;
        text-align: center;
        font-size: 16px;
        color: #fff;
    }
    form {
        margin: 30px auto;
        padding: 24px 20px;
        max-width: 660px;
        border-radius: 5px;
        background-color: #f2f5f8;
        text-align: center;
        input {
            padding: 16px;
            border: 2px solid #c6ccd2;
            border-radius: 5px;
            &:not(:first-child) {
                margin-left: 20px;
            }
        }
        [type="submit"] {
            padding: 18px 44px;
            background-color: #d9c26c;
            border: none;
            color: #fff;
        }
    }
}

.footer-tenth__menu {
    margin-top: 78px;
    text-align: center;
    a {
        color: #fff;
        font-size: 14px;
        text-transform: uppercase;
        text-decoration: none;
        &:not(:first-child) {
            margin-left: 58px;
        }
    }
}

.copyright {
    margin-top: 28px;
    padding-bottom: 38px;
    color: rgba(255, 255, 255, 0.502);
}
